<template>
  <div class="success" :class="{'scrollAnimate':isScroll}">
    <div class="header">
      <img class="logo" src="./../assets/home/logo.png" alt=""/>
      <p>ID:{{userInfo.player_id}}</p>
    </div>
    <div class="portrait">
      <img v-if="userInfo.avatar"
        :src="userInfo.avatar"
        alt="">
      <img class="defaultPortrait" v-else="" src="./../assets/success/icon_portrait.png" alt="">
    </div>
    <h3>{{userInfo.nick_name}}</h3>
    <p class="bip"><i class="iconfont icon-jinbi"></i><span>{{userInfo.coins}}</span></p>
    <template v-if="!isReceive">
      <h4>恭喜获得</h4>
      <h5 v-if="rain.type=='money'">{{rain.value}}元现金红包</h5>
      <h5 v-if="rain.type=='coin'">{{rain.value}}个免费游戏币</h5>
    </template>
    <template v-else="">
      <h2>您已经领取过<span v-if="rain.type=='money'">{{rain.value}}元现金红包啦</span><span v-if="rain.type=='coin'">{{rain.value}}个游戏币啦</span></h2>
      <h6>快去寻宝吧~</h6>
    </template>
    <div class="btn" @click="goDown">点我寻宝</div>
    <img class="mainImg" src="http://res.catchme.com.cn/activity/rain/gaozhi_01.png" alt="">
    <img class="mainImg" src="http://res.catchme.com.cn/activity/rain/gaozhi_02.png" alt=""/>
    <img class="mainImg" src="http://res.catchme.com.cn/activity/rain/gaozhi_03.png" alt=""/>
    <img class="mainImg" src="http://res.catchme.com.cn/activity/rain/gaozhi_04.png" alt=""/>
    <img id="map" class="mainImg" src="http://res.catchme.com.cn/activity/rain/gaozhi_05.png" alt=""/>
  </div>
</template>

<script>
  import api from './../api'
  export default {
    name: 'success',
    data(){
      return {
        userInfo:{
          player_id:'',
          avatar:'',
          nick_name:'',
          coins:0
        },
        isScroll:false,
        rain:{
          type:'',
          value:''
        },
        isReceive:false
      }
    },
    mounted(){
      //获取用户信息
      this.rain = this.$route.params.rain;
      if(this.rain){
        localStorage.setItem('rain',JSON.stringify(this.rain))
      }else {
        this.rain = JSON.parse(localStorage.getItem('rain'))
//        已经领取过了
        this.isReceive = true;
      }
      api.getUser({}).then((res)=>{
        this.userInfo = res.data
      })
    },
    methods:{
      goDown(){
        var height=0;
        var startTime = Date.now();
        var duration = 1000;
        var distance = document.body.scrollHeight;
        requestAnimationFrame(function step() {
          var p = Math.min(1.0, (Date.now() - startTime) / duration);
          height = distance*p;
          window.scrollTo(0,height)
          if(height<document.body.scrollHeight){
            requestAnimationFrame(step)
          }
        })
      },
    }
  }
</script>

<style lang="scss" scoped="">
  @mixin center {
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
  }
  @mixin centerX {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  @keyframes scroll
  {
    from {scroll-top: 0px;}
    to {scroll-top: 1000px;}
  }

  .success {
    animation: scroll 1s;
    width: 100%;
    /*height: 100%;*/
    background: url("./../assets/success/rectangle.png");
    background-size: 41px 41px;
    color: #353535;
    overflow: auto;
    position: relative;
    .header {
      width: 100%;
      padding: 0.1px;
      position: absolute;
      left: 0;
      top: 0;
      .logo {
        width: 252px;
        height: 74px;
        margin: 12px 0 0 30px;
        display: inline-block;
      }
      p {
        float: right;
        margin: 36px 0 0 0;
        font-size: 24px;
        line-height: 24px;
        padding: 10px 0;
        width: 169px;
        text-align: center;
        background: #ffe3e5;
        color: #e07078;
        border-radius: 22px 0 0 22px;
      }
    }
    .portrait {
      width: 121px;
      height: 121px;
      background-color: #feccbc;
      border: solid 4px #ffffff;
      border-radius: 50%;
      overflow: hidden;
      @include centerX;
      top: 66px;
      img {
        width: 100%;
      }
      .defaultPortrait{
        width: 88px;
        @include center
      }
    }
    .btn{
      width: 360px;
      height: 70px;
      background-image: linear-gradient(0deg,
        rgba(239, 70, 98, 0.65) 0%,
        rgba(250, 110, 120, 0.65) 100%),
      linear-gradient(
          #ff8e96,
          #ff8e96);
      background-blend-mode: normal,
      normal;
      border-radius: 35px;
      @include centerX;
      top:803px;
      font-size: 32px;
      color: #fff;
      text-align: center;
      line-height: 70px;
    }
    h3 {
      @include centerX;
      font-size: 34px;
      line-height: 34px;
      top: 200px;
    }
    .bip {
      height: 44px;
      line-height: 44px;
      background: #fef7f7;
      border-radius: 22px;
      font-size: 36px;
      padding: 0 43px;
      @include centerX;
      top: 242px;
      display: flex;
      align-items: center;
      span{
        display: inline-block;
        vertical-align: middle;
      }
      i {
        font-size: 40px;
        margin: 0 4px 0 0;
        vertical-align: middle;
      }
    }
    > h4 {
      width: 100%;
      text-align: center;
      font-size: 40px;
      color: #c08d5e;
      line-height: 40px;
      @include centerX;
      top: 380px;
      &.es{
        top:353px;
        font-size: 74px;
      }
    }
    > h5 {
      text-align: center;
      width: 100%;
      font-size: 74px;
      line-height: 74px;
      color: #c08d5e;
      @include centerX;
      top: 444px;
      &.es{
        top:409px;
        font-size: 40px;
      }
    }
    >h2{
      @include centerX;
      top: 394px;
      width: 100%;
      text-align: center;
      font-size: 40px;
      line-height: 40px;
      color: #c08d5e ;
    }
    >h6{
      @include centerX;
      top: 460px;
      width: 100%;
      text-align: center;
      font-size: 40px;
      line-height: 40px;
      color: #c08d5e ;
    }
    .mainImg {
      width: 750px;
    }
  }
</style>



